.drawCard {
    text-align: center;
}

.cardList {
    display: flex;
    justify-content: center;

    height: 250px;
    box-sizing: border-box;

    -webkit-user-select: none;
    user-select: none;
}

.cardImg {
    border: 1px solid #fff;
    background-color: #fff;
}

.rarity-6 {
    // border: 1px solid rgb(255, 189, 8);
    background-color: rgb(255, 148, 8);
}

.rarity-5 {
    // border: 1px solid rgb(255, 119, 8);
    background-color: rgb(255, 210, 8);
}

.rarity-4 {
    // border: 1px solid rgb(53, 8, 255);
    background-color: rgb(53, 8, 255);
}

.btn_container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

.btn_draw {
    width: 150px;
    height: 50px;
    border: 0;
    background-color: none;

    -webkit-user-select: none;
    user-select: none;
}
